.about__container {
  display: grid;
  grid-template-columns: 45% 40%;
  gap: 15%;
}

.about__me {
  width: 100%;
  min-width: 320px;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about__me-image:hover {
  transform: rotate(0deg);
}

.about__me-image > img {
  object-position: 0% -100%;
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.merge__card {
  grid-row: 2 / span 2;
  grid-column: 1 / span 2;
}

.about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 1.5rem;
  text-align: center;
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about__card h5 {
  font-size: 1rem;
}

.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
  text-align: center;
}

a {
  color: var(--color-light);
}

/* --- Media Queries (Medium Devices) --- */

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }

  /* .about__content {
    
  } */
  .cta-chat {
    display: block;
    margin: 0 auto; /* Center the button horizontally */
  }
}

/* --- Media Queries (Small Devices) --- */

@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards p {
    grid-template-columns: 1fr 1fr;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
}
